import React from "react"
import styled from "styled-components"
import { graphql, useStaticQuery } from "gatsby"
import ProductIcon from "../atoms/ProductIcon"
import ProductDescription from "../atoms/ProductDescription"
import imagesData from "../../../imagesData.json"

const StyledWrapper = styled.div`
  display: flex;
  flex-direction: column;
  text-align: center;
`
const ProductsWrapper = styled.div`
  justify-content: center;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 450px));
  grid-auto-rows: 2px;
  grid-gap: 30px;
`

const Header = styled.div`
  color: black;
  cursor: default;
  letter-spacing: 2px;
  font-size: 70px;
  margin-bottom: 30px;
  font-family: "Poiret One", cursive;
  font-weight: 600;
  text-align: center;
`

const EmptyView = styled.h2`
  grid-column: 1/-1;
  margin: auto;
  color: black;
  cursor: default;
  font-size: 50px;
  font-weight: 600;
`

const Products = () => {
  const products = useStaticQuery(graphql`
    {
      allFile(
        filter: {
          extension: { in: ["png", "jpg", "jpeg"] }
          sourceInstanceName: { eq: "products" }
        }
      ) {
        edges {
          node {
            sourceInstanceName
            relativePath
            publicURL
            name
            absolutePath
            childImageSharp {
              small: fixed(width: 450) {
                ...GatsbyImageSharpFixed
              }
              large: fixed(height: 700) {
                ...GatsbyImageSharpFixed
              }
            }
          }
        }
      }
    }
  `).allFile.edges
  console.log(products)
  return products && products.length > 0 ? (
    <>
      <Header>Produkty</Header>
      <ProductsWrapper className="products">
        {products.map((product, index) => {
          const title = imagesData[product.node.name]?.title ?? ""
          const description = imagesData[product.node.name]?.description ?? ""
          return (
            <div
              key={index}
              style={{ gridRowEnd: "span 250" }} // Average height of a grid-item to mitigate resizing consquences
              className="grid-item"
            >
              <StyledWrapper className="content">
                <ProductIcon photo={product.node.childImageSharp} alt={title} />
                <ProductDescription title={title} description={description} />
              </StyledWrapper>
            </div>
          )
        })}
      </ProductsWrapper>
    </>
  ) : (
    <EmptyView>Brak produktw</EmptyView>
  )
}

export default Products
